footer {
    position: fixed;
    background: #0f5a33;
    width: 100vw;
    bottom: 0;
    
    .service_call {
        position: fixed;
        bottom: 2vh;
        right: 2vw;
        border: dotted 0.1rem;
        border-color: #0f5a33;
        border-radius: 50%;
        height: 5rem;
        width: 5rem;
        background: linear-gradient(0deg, #62d84a, #3a9927);
    }
}