body {
  margin: 0;
  padding: 0;

  #root {
    overscroll-behavior-y: contain;

    h1,
    p {
      font-family: agbalumo;
    }

    main {
      position: relative;
      z-index: 0;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-top: 2rem;

      .menu {
        margin: 1rem 0vw 0rem 0vw;
        padding: 0;
        background: linear-gradient(#c38822, #b13325);
        background-clip: text;
        color: transparent;
        font-family: agbalumo;
        font-size: 2rem;
      }

      .category {
        background: #cce3ee94;
        border-top: solid 0.2rem #e9ac89;
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 10rem;
        width: max-content;
        overscroll-behavior-x: contain;

        .title {
          display: flex;
          flex-direction: column;
          align-items: center;
          text-align: center;
          width: 20vw;
          margin-left: 2rem;

          .img {
            filter: drop-shadow(0.1rem 0.1rem 0.1rem #162502);
            height: 7rem;
          }
        }

        &.coffee .title {
          color: #a85816cb;
        }

        &.tea .title {
          color: #5d7e3e;
        }

        &.iceTea .title {
          color: #528cff;
        }

        &.matcha .title {
          color: #0c632d;
        }

        &.smoothies {
          color: #ec3c42;
        }

        &.lemonades .title {
          color: #ecd507;
        }

        &.waffles .title {
          color: #9a7299;
        }

        &.toasts .title {
          color: #c9a54b;
        }

        &.panini .title {
          color: #ecae3c;
        }

        &.wraps .title {
          color: #b98005;
        }

        &.desserts .title {
          color: #8b0c58;
        }
      }
    }
  }
}
