header {
  background: linear-gradient(0deg, #62d84a, #3a9927);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  height: rem;
  width: 100vw;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;

  .company {
    display: flex;
    flex-direction: column;
    align-items: center;

    .logo {
      height: 6rem;
    }

    .name {
      margin-top: -1rem;
      height: 2rem;
    }
  }

  .basket {
    background: none;
    border: none;
    right: 5vw;
    position: absolute;

    .img {
      height: 6rem;
      width: auto;
    }
  }
}
